// Load fonts with @font-face rule
// Use Arial as fallback font

@include font-face($base-font, #{$font-path}/Roboto-Medium, 500, normal, eot woff woff2 ttf);
@include font-face($base-font, #{$font-path}/Roboto-Regular, 400, normal, eot woff woff2 ttf);
@include font-face($base-font, #{$font-path}/Roboto-Light, 300, normal, eot woff woff2 ttf);
